import { PropsWithChildren, useCallback } from "react"
import { useTranslation } from "react-i18next"
import { Uploadable } from "@rp/common/utils"
import { UploadFilesProvider } from "@rp/react/common/providers"
import { useSnackbar } from "../Snackbar"

export const WebAppFilesProvider = ({ children }: PropsWithChildren) => {
  const { enqueueError } = useSnackbar()
  const { t } = useTranslation()
  const onUploadError = useCallback(
    (next: Uploadable) => {
      enqueueError(t("filePage.uploadFileError", { fileName: next.file.name }))
    },
    [enqueueError, t],
  )
  const onQuotaError = useCallback(
    (message: string) => {
      enqueueError(message, { autoHideDuration: 5000 })
    },
    [enqueueError],
  )

  return (
    <UploadFilesProvider onUploadError={onUploadError} onQuotaError={onQuotaError}>
      {children}
    </UploadFilesProvider>
  )
}
